import { defineStore } from 'pinia'
import { Message, MessageBox } from 'element-ui'
import { useStoreConfig } from './configStore'
import _ from 'lodash'

import {
  get as getSettings,
  update as updateSettings
} from '@/api/site/settings'
import {
  show as showSeo,
  update as updateSeo,
  store as storeSeo
} from '@/api/site/seo'
import {
  get as getCss,
  store as storeCss,
  updateAll as updateCss,
  destroy as deleteCss,
  getCustom as getCustomCss,
  updateCustom as updateCustomCss
} from '@/api/site/css'
import {
  get as getJavascript,
  store as storeJavascript,
  updateAll as updateJavascript,
  destroy as deleteJavascript,
  getCustom as getCustomJavascript,
  updateCustom as updateCustomJavascript
} from '@/api/site/javascript'
import {
  getFiles as getAssets
} from '@/api/utilities/files/assets'
import { showKeyName } from '@/api/site/index'

// import { show as showSitemap } from '@/api/site/sitemap'
const configStore = useStoreConfig()

export const useStoreSite = defineStore('site', {
  state: () => ({
    configStore,
    loading: false,
    isSeoUpdate: true,
    keyNameSite: '',
    seoTempleteBlank: {
      description: '',
      image: '',
      metaTag: [],
      lang: '',
      title: '',
      schema: ''
    },
    langDefault: '',
    settings: {
      name: '',
      baseUri: '',
      lang: '',
      robots: false,
      favicon: ''
    },
    seo: {
      description: '',
      image: '',
      metaTag: [],
      title: '',
      schema: ''
    },
    css: [],
    javascript: [],
    file: {
      name: '',
      pathFile: '',
      type: '',
      order: 0
    },
    customContentCss: '',
    customContentJavascript: '',
    assets: [],
    lang: {
      title: '',
      description: '',
      metaTag: '',
      image: '',
      schema: ''
    },
    sitemapMock: {
      es: [
        {
          menuTitle: 'Inicio',
          keyNamePage: 'home',
          slug: 'inicio',
          keyNameHeader: 'home',
          keyNameMain: 'home',
          keyNameFooter: 'home',
          type: 'page',
          urlRedirec: '',
          target: '_self',
          isVisible: true
        },
        {
          keyNamePage: 'services',
          menuTitle: 'Servicios',
          slug: 'services',
          keyNameTemplete: 'service-landing',
          type: 'page',
          urlRedirec: '',
          target: '_self',
          isVisible: true,

          children: [
            {
              keyNamePage: 'service-1',
              menuTitle: 'Servicio 1',
              slug: 'services-1',
              keyNameTemplete: 'service-inner',
              type: 'page',
              urlRedirec: '',
              target: '_self',
              isVisible: true
            },
            {
              keyNamePage: 'service-2',
              menuTitle: 'Servicio 2',
              slug: 'services-2',
              keyNameTemplete: 'service-inner',
              type: 'page',
              urlRedirec: '',
              target: '_self',
              isVisible: true
            }
          ]
        },
        {
          keyNamePage: 'blog',
          menuTitle: 'Blogs',
          slug: 'blog',
          keyNameTemplete: 'blog-landing',
          type: 'page',
          urlRedirec: '',
          target: '_self',
          isVisible: true,

          children: [
            {
              keyNamePage: 'blog-1',
              menuTitle: 'Blog 1',
              slug: 'blog-1',
              keyNameTemplete: 'bog-inner',
              type: 'page',
              urlRedirec: '',
              target: '_self',
              isVisible: true
            },
            {
              keyNamePage: 'blog-2',
              menuTitle: 'Blog 2',
              slug: 'blog-2',
              keyNameTemplete: 'bog-inner',
              type: 'page',
              urlRedirec: '',
              target: '_self',
              isVisible: true
            }
          ]
        }
      ],
      en: [
        {
          keyNamePage: 'home',
          menuTitle: 'Home',
          slug: 'home',
          keyNameTemplete: 'home',
          type: 'page',
          urlRedirec: '',
          target: '_self',
          isVisible: true
        },
        {
          keyNamePage: 'services',
          menuTitle: 'services',
          slug: 'services',
          keyNameTemplete: 'service-landing',
          type: 'page',
          urlRedirec: '',
          target: '_self',
          isVisible: true
        }
      ]
    },
    sitemap: [],
    langSelected: '',
    // List Options
    optionsTags: [
      {
        value: 'Tecnología',
        label: 'Tecnología'
      },
      {
        value: 'Seguridad',
        label: 'Seguridad'
      },
      {
        value: 'Vigilancia',
        label: 'Vigilancia'
      },
      {
        value: 'Alertas',
        label: 'Alertas'
      }
    ]
  }),
  actions: {
    // Settings
    async fetchSettings () {
      this.loading = true
      return await getSettings()
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          this.settings = response.data
          return this.settings
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async updateSettings (settingsData) {
      this.loading = true
      return await updateSettings(settingsData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.settings = response.data

          return this.settings
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },

    // SEO
    async fetchSeo () {
      await showSeo(configStore.langSelected)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          if (response.success) {
            this.isSeoUpdate = true
            this.seo = response.data
          } else {
            this.isSeoUpdate = false
            this.seo = _.cloneDeep(this.seoTempleteBlank)
          }
          return this.seo
        })
        .catch((e) => {
          console.log('responseCatch -> data', e)
        })
      // .catch(this.responseCatch)
    },
    async updateSeo (seoData, lang) {
      this.loading = true
      return await updateSeo(seoData, lang)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.seo = response.data
          this.isSeoUpdate = true

          return this.seo
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },

    async storeSeo () {
      this.loading = true
      const seoData = _.cloneDeep(this.seo)
      return await storeSeo(seoData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })
          // this.seo = response.data
          this.isSeoUpdate = true

          this.seo = response.data

          return this.seo
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },

    // CSS
    async fetchCss () {
      this.loading = true
      return await getCss()
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          this.css = response.data

          return this.css
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async storeCssFile () {
      this.loading = true
      const fileData = _.cloneDeep(this.file)
      return await storeCss(fileData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.css = response.data

          return this.css
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async updateCssFiles (cssData) {
      this.loading = true
      return await updateCss({ files: cssData })
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.css = response.data

          return this.css
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async deleteCssFile (node, data) {
      MessageBox.confirm(
        'Se borrara el arvhivo de su sitio, ¿Esta seguro?',
        'Cuidado',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancelar',
          type: 'warning'
        }
      )
        .then(() => {
          this.loading = true
          const fileData = _.cloneDeep(this.file)
          return deleteCss(fileData, data._id)
            .then((response) => {
              if (!response.success) {
                Message({
                  showClose: true,
                  message: response.messages[0] || 'Error',
                  type: 'danger'
                })
                return false
              }
              Message({
                showClose: true,
                message: response.messages[0] || 'Exito',
                type: 'success'
              })

              const parent = node.parent
              const children = parent.data.children || parent.data
              const index = children.findIndex((d) => d._id === data._id)
              children.splice(index, 1)

              this.css = response.data

              return this.css
            })
            .catch(this.responseCatch)
        })
        .catch(() => {
          Message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operación'
          })
        })
        .finally(() => (this.loading = false))
    },
    orderFilesCss () {
      this.css.forEach((el, index) => {
        el.order = index + 1
      })
    },
    async fetchCustomCss () {
      this.loading = true
      return await getCustomCss()
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          this.customContentCss = response.data.content

          return this.customContentCss
        })
        .catch(this.responseCatch)
        .finally(() => {
          this.loading = false
        })
    },
    async updateCustomCss (customData) {
      this.loading = true
      return await updateCustomCss({ content: customData })
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.customContentCss = response.data.content

          return this.customContentCss
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },

    // JavaScript
    async fetchJavascript () {
      this.loading = true
      return await getJavascript()
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          this.javascript = response.data

          return this.javascript
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async storeJavascriptFile () {
      this.loading = true
      const fileData = _.cloneDeep(this.file)
      return await storeJavascript(fileData)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.javascript = response.data

          return this.javascript
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async updateJavascriptFiles (javascriptData) {
      this.loading = true
      return await updateJavascript({ files: javascriptData })
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          Message({
            showClose: true,
            message: response.messages[0] || 'Exito',
            type: 'success'
          })

          this.javascript = response.data

          return this.javascript
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    async deleteJavascriptFile (node, data) {
      MessageBox.confirm(
        'Se borrara el arvhivo de su sitio, ¿Esta seguro?',
        'Cuidado',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancelar',
          type: 'warning'
        }
      )
        .then(() => {
          this.loading = true
          return deleteJavascript(data._id)
            .then((response) => {
              if (!response.success) {
                Message({
                  showClose: true,
                  message: response.messages[0] || 'Error',
                  type: 'danger'
                })
                return false
              }
              Message({
                showClose: true,
                message: response.messages[0] || 'Exito',
                type: 'success'
              })
              const parent = node.parent
              const children = parent.data.children || parent.data
              const index = children.findIndex((d) => d._id === data._id)
              children.splice(index, 1)

              this.javascript = response.data

              return this.javascript
            })
            .catch(this.responseCatch)
        })
        .catch(() => {
          Message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operación'
          })
        })
        .finally(() => (this.loading = false))
    },
    orderFilesJavascript () {
      this.javascript.forEach((el, index) => {
        el.order = index + 1
      })
    },
    async fetchCustomJavascript () {
      this.loading = true
      return await getCustomJavascript()
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }

          this.customContentJavascript = response.data.content

          return this.customContentJavascript
        })
        .catch(this.responseCatch)
        .finally(() => {
          this.loading = false
        })
    },
    async updateCustomJavascript (customData) {
      this.loading = true
      return await updateCustomJavascript({
        content: customData
      })
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error!',
              type: 'warning'
            })

            return false
          }

          Message({
            showClose: true,
            message: response.messages[0] || 'Exito!',
            type: 'success'
          })
          this.customContentJavascript = response.data.content

          return this.customContentJavascript
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    // Assets
    async fetchAssets (data) {
      this.loading = true
      return await getAssets(data)
        .then((response) => {
          if (!response.success) {
            Message({
              showClose: true,
              message: response.messages[0] || 'Error',
              type: 'danger'
            })
            return false
          }
          this.assets = response.data
          return this.assets
        })
        .catch(this.responseCatch)
        .finally(() => (this.loading = false))
    },
    // Side Map
    fetchSitemap () {
      this.loading = true
      const configStore = useStoreConfig()

      this.sitemap = this.sitemapMock[configStore.langSelected]

      // return await showSitemap(configStore.langSelected)
      //   .then(response => {
      //     this.sitemap = response.data
      //     console.log(this.sitemap)
      //     // return this.javascript
      //   })
      //   .catch(this.responseCatch)
      //   .finally(() => (this.loading = false))
    },
    async showKeyName () {
      return await showKeyName().then((response) => {
        if (!response.success) {
          Message({
            showClose: true,
            message: response.messages[0] || 'Error',
            type: 'danger'
          })
          return false
        }
        Message({
          showClose: true,
          message: response.messages[0] || 'Exito',
          type: 'success'
        })
        this.keyNameSite = response.data

        return this.keyNameSite
      })
    }
  }
})
