import request from '@/utils/request'

export function index () {
  return request({
    url: '/site/css',
    method: 'get'
  })
}

export function get () {
  return request({
    url: '/site/css/files',
    method: 'get'
  })
}

export function show (id) {
  return request({
    url: '/site/css/files/' + id,
    method: 'get'
  })
}

export function store (data) {
  return request({
    url: '/site/css/files',
    method: 'post',
    data
  })
}

export function update (data, id) {
  return request({
    url: '/site/css/files/' + id,
    method: 'put',
    data
  })
}

export function updateAll (data) {
  return request({
    url: '/site/css/files/all',
    method: 'put',
    data
  })
}

export function destroy (data, id) {
  return request({
    url: '/site/css/files/' + id,
    method: 'delete',
    data
  })
}

export function getCustom () {
  return request({
    url: '/site/css/custom',
    method: 'get'
  })
}

export function updateCustom (data) {
  return request({
    url: '/site/css/custom',
    method: 'put',
    data
  })
}
