import request from '@/utils/request'

export function index () {
  return request({
    url: '/site/javascript',
    method: 'get'
  })
}

export function get () {
  return request({
    url: '/site/javascript/files',
    method: 'get'
  })
}

export function show (id) {
  return request({
    url: '/site/javascript/files/' + id,
    method: 'get'
  })
}

export function store (data) {
  return request({
    url: '/site/javascript/files',
    method: 'post',
    data
  })
}

export function update (data, id) {
  return request({
    url: '/site/javascript/files/' + id,
    method: 'put',
    data
  })
}

export function updateAll (data) {
  return request({
    url: '/site/javascript/files',
    method: 'put',
    data
  })
}

export function destroy (id) {
  return request({
    url: '/site/javascript/files/' + id,
    method: 'delete'
  })
}

export function getCustom () {
  return request({
    url: '/site/javascript/custom',
    method: 'get'
  })
}
export function updateCustom (data) {
  return request({
    url: '/site/javascript/custom',
    method: 'put',
    data
  })
}
