import request from '@/utils/request'

export function upload (data) {
  return request({
    url: '/utilities/assets/upload',
    method: 'post',
    data
  })
}

export function getFiles (data) {
  return request({
    url: '/utilities/assets/files',
    method: 'post',
    data
  })
}

export function removeFolder (data) {
  return request({
    url: '/utilities/assets/folder/remove',
    method: 'post',
    data
  })
}

export function removeFile (data) {
  return request({
    url: '/utilities/assets/file/remove',
    method: 'post',
    data
  })
}
