import request from '@/utils/request'

export function get () {
  return request({
    url: '/site/seo',
    method: 'get'
  })
}

export function show (lang) {
  return request({
    url: '/site/seo/' + lang,
    method: 'get'
  })
}

export function update (data, lang) {
  return request({
    url: '/site/seo/' + lang,
    method: 'put',
    data
  })
}

export function store (data) {
  return request({
    url: '/site/seo',
    method: 'post',
    data
  })
}

export function destroy (lang) {
  return request({
    url: '/site/seo/' + lang,
    method: 'delete'
  })
}
